//This is the root data model for the sequence diagram
function ToWalkerFactory(canvasManager) {
    var SequenceListener = require('generated-parser/sequenceListener');
    var ToWalker = function () {
        SequenceListener.sequenceListener.call(this);
        return this;
    };

    ToWalker.prototype = Object.create(SequenceListener.sequenceListener.prototype);
    ToWalker.prototype.constructor = ToWalker;
    canvasManager.addEntity('CLIENT');
    ToWalker.prototype.enterTo = function (ctx) {
        var to = ctx.getText();
        canvasManager.addEntity(to);
    };
    return ToWalker;
}
function MessageWalkerFactory(canvasManager) {
    var SequenceListener = require('generated-parser/sequenceListener');
    var MessageWalker = function () {
        SequenceListener.sequenceListener.call(this);
        return this;
    };

    var nextTop = 90;
    var tops = [nextTop];
    var pMessages = [new PresentationMessage('ROOT', 'ROOT', 'ROOT')];
    MessageWalker.prototype = Object.create(SequenceListener.sequenceListener.prototype);
    MessageWalker.prototype.constructor = MessageWalker;
    MessageWalker.prototype.enterMessage = function (ctx) {
        function getToFirstAncestor() {
            function toFound(ancestor) {
                return ancestor && ancestor.to && ancestor.to()
            }

            // One ancestor must have a 'to'
            var ancestor = ctx.parentCtx;
            while (!toFound(ancestor)) {
                ancestor = ancestor.parentCtx;
            }
            return ancestor.to().getText();
        }

        var entityFrom = 'CLIENT';
        if (ctx.parentCtx && (ctx.parentCtx.ruleIndex !== 0)) {
            entityFrom = getToFirstAncestor();
        }
        var entityTo = (ctx.to() && ctx.to().getText()) || entityFrom;
        var methodName = ctx.methodName().getText();

        var pMessage = new PresentationMessage(entityFrom, entityTo, methodName);
        var last = pMessages[pMessages.length - 1];
        last.addSubMessage(pMessage);
        pMessages.push(pMessage);
        tops.push(nextTop);
        nextTop += pMessage.getHeight();
    };

    MessageWalker.prototype.exitMessage = function (ctx) {
        canvasManager.drawPresentationMessage(pMessages.pop(), tops.pop());
    };
    return MessageWalker;
}
function ActivitySequence() {
    this.draw = function(canvasManager, scriptContent) {

        var antlr4 = require('antlr4/index');
        var SequenceLexer = require('generated-parser/sequenceLexer');
        var SequenceParser = require('generated-parser/sequenceParser');
        var chars = new antlr4.InputStream(scriptContent);
        var lexer = new SequenceLexer.sequenceLexer(chars);
        var tokens = new antlr4.CommonTokenStream(lexer);
        var parser = new SequenceParser.sequenceParser(tokens);
        parser.buildParseTrees = true;
        var tree = parser.prog();

        var ToWalker = ToWalkerFactory(canvasManager);
        antlr4.tree.ParseTreeWalker.DEFAULT.walk(new ToWalker(), tree);

        var MessageWalker = MessageWalkerFactory(canvasManager);
        antlr4.tree.ParseTreeWalker.DEFAULT.walk(new MessageWalker(), tree);
    }
}
