var nomnoml = nomnoml || {};

(function () {
	'use strict';

	function getConfig(d) {
		return {
			arrowSize: 0.5,
			bendSize:  0.3,
			direction: 'TB',
			gutter:  5,
			edgeMargin:  0,
			edges: 'rounded',
			fill: ('#21252b;#002b36;#21252b;#002b36').split(';'),
			fillArrows: 'true',
			font:  'Calibri',
			fontSize:  12,
			leading: 1.25,
			lineWidth:  2,
			padding:  8,
			spacing:  70,
			stroke:  '#aaaaaa',
			title:  'jhipster-jdl',
			zoom: 1
		};
	}

	function fitCanvasSize(canvas, rect, zoom) {
		canvas.width = rect.width * zoom;
		canvas.height = rect.height * zoom;
	}

	function setFont(config, isBold, isItalic, graphics) {
		var style = (isBold === 'bold' ? 'bold' : '')
		if (isItalic) style = 'italic' + style
		var defaultFont = 'Helvetica, sans-serif'
		var font = skanaar.format('# #pt #, #', style, config.fontSize, config.font, defaultFont)
		graphics.ctx.font = font
	}

	function parseAndRender(code, graphics, canvas, scale, canvasManager, viewport) {
		// var ast = nomnoml.parse(code);
		var ast = {};
		var config = getConfig(ast.directives);
		var measurer = {
			setFont: function (a, b, c) { setFont(a, b, c, graphics); },
			textWidth: function (s) { return graphics.ctx.measureText(s).width },
			textHeight: function () { return config.leading * config.fontSize }
		};
		var layout = {width: viewport.innerWidth, height: viewport.innerHeight};
		fitCanvasSize(canvas, layout, config.zoom * scale);
		config.zoom *= scale;
        var activitySequence = new ActivitySequence();
        activitySequence.draw(canvasManager, code);

        // nomnoml.render(graphics, config, layout, measurer.setFont);
		return { config: config };
	}

	nomnoml.draw = function (canvas, code, scale, viewport) {
        var canvasManager = new CanvasManager(canvas);

        var skCanvas = skanaar.Canvas(canvas)
		return parseAndRender(code, skCanvas, canvas, scale || 1, canvasManager, viewport)
	};
})();
