function BarDrawer() {
    this.draw = function(context, left, top, height) {
        var barWidth = 10;
        context.beginPath();
        hand_drawing_rect(context, left - barWidth/2, top, barWidth, height, 2);
        context.lineWidth = 2;
        context.strokeStyle = "#FFF";
        context.stroke();
        context.fillStyle = "#FFF";
        context.closePath();
        context.fill();
        context.fillStyle = null;

    }
}