function CanvasManager(canvasEle) {
    this.lifeLineDrawer = new LifeLineDrawer();
    this.messageDrawer = new MessageDrawer();
    this.barDrawer = new BarDrawer();
    this.entitySpace = 60.1;
    this.lifeLenght = 2000;
    this.rightBound = 0;
    this.entities = [];

    var context = canvasEle.getContext('2d');

    this.addEntity = function(entityName) {
        if (!!this.getEntity(entityName)) return;
        var newLeft = this.rightBound + this.entitySpace;
        var entityWidth = this.lifeLineDrawer.draw(context, entityName, newLeft, this.lifeLenght);
        this.rightBound = newLeft + entityWidth;
        var entity = new RichEntity(entityName, newLeft, entityWidth);
        this.entities.push(entity);
    };

    this.getEntity = function(entityName) {
        return _.find(this.entities, _.matchesProperty('name', entityName));
    };

    this.removeAllEntities = function() {
        if (canvasEle) {
            context.clearRect(0, 0, canvasEle.width, canvasEle.height)
        }
        this.entities.length = 0;
        this.rightBound = 0;
    };

    this.drawPresentationMessage = function(presentationMessage, top) {
        var entityFrom = this.getEntity(presentationMessage.fromEntity);
        var entityTo = this.getEntity(presentationMessage.toEntity);
        if (entityFrom == entityTo) {
            var left = entityFrom.left + entityFrom.width / 2;
            new InternalInvokeDrawer()
                    .draw(context, presentationMessage.messageText, left, top);
            this.barDrawer.draw(context, left, top + 30, presentationMessage.getBarHeight())

        } else {
            var start = entityFrom.left + entityFrom.width / 2;
            var end = entityTo.left + entityTo.width / 2;
            var length = end - start;
            this.messageDrawer.draw(context, presentationMessage.messageText, start, top, length);
            this.barDrawer.draw(context, end, top, presentationMessage.getBarHeight());
        }
    };

}

