function LifeLineDrawer() {
    this.draw = function(context, entityName, left, lifeLength) {
        var top = 20;
        var entityHeight = 30;

        var entityDrawer = new EntityDrawer();
        var entityWidth = entityDrawer.draw(context, entityName, left, top);

        var lineDrawer = new LineDrawer();
        lineDrawer.draw(context,left + (entityWidth)/2, top + entityHeight, lifeLength);

        return entityWidth;
    }
}
