function MessageParser() {

    function tokenize(scriptContent) {
        scriptContent = scriptContent.replace(/{/g, "\n{\n");
        scriptContent = scriptContent.replace(/}/g, "\n}\n");

        return scriptContent.split("\n").map(function (expression) {
            return expression.replace(/\s/g, '')
        });
    }

    function getMessage(sentence) {
        /((\w+):)*((\w+)=)*((\w+)\.)*([\w(),]+)(;?)/.test(sentence);

        var entityFrom = RegExp.$2;
        var result = RegExp.$4;
        var entityTo = RegExp.$6;
        var message = RegExp.$7;

        entityTo = entityTo || entityFrom;
        return SyncMessage(entityFrom, entityTo, message, result);
    }

    var me = function Me() {
        var virtualRoot = SyncMessage();
        var current = virtualRoot;
        var messageStack = [current];
        return {
            enter: function () {
                messageStack.push(current);
            },
            exit: function () {
                messageStack.pop();
            },
            add: function (message) {
                var topOnStack = messageStack[messageStack.length - 1];
                if (messageStack.length == 1) {
                    message.ensureFrom("CLIENT");
                } else {
                    message.ensureFrom(topOnStack.to);
                }
                console.log(messageStack.length);
                topOnStack.subMessages.push(message);
                current = message;
            },
            getRootMessages: function () {
                return virtualRoot.subMessages;
            }
        }
    }();

    return {
        parse: function (scriptContent) {
            var expressions = tokenize(scriptContent);
            expressions.forEach(function (expression) {
                if (expression === undefined || expression === "" || expression.startsWith("//") || expression.startsWith(":")) return;
                if (expression === "{") {
                    me.enter();
                } else if (expression === "}") {
                    me.exit();
                } else {
                    me.add(getMessage(expression));
                }
            });
            return me.getRootMessages();
        }
    };
}
