function SyncMessage(from, to, message, result, parent) {
    return {
        ensureFrom: function (defaultFrom) {
            this.from = from || defaultFrom;
            this.to = to || this.from;
        },
        subMessages: [],
        from: from,
        to: to,
        message: (result && "[" + result + "]") + message,
        parent: parent
    };
}
